.searchtop__wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 2rem;
    align-items: center;
}

.searchtop__left{
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.searchtop__centre{
    flex: 1;
    align-items: center;
    display: flex;
    justify-content: center;
}

.searchtop__right{
    flex: 1;
}

form.search__containeer {
    border: 1px solid gray;
    align-items: center;
    display: flex;
    padding: 5px;
    border-radius: 5px;
    margin-left: 25px;
}

.searchtop__centre h1 {
    font-weight: bold;
    color: black;
}

.searchtop__centre {
    flex: 1;
    align-items: center;
    display: flex;
    justify-content: center;
}

.searchtop__right {
    flex: 1;
    justify-content: flex-end;
    display: flex;
    align-items: center;
    gap: 10px;
}

.menu__item {
    margin-right: 13px;
}

.cart {
    position: relative;
    z-index: -2;
}

.menu__item.cart span {
    position: absolute;
    top: -7px;
    right: -7px;
    height: 17px;
    width: 17px;
    background: teal;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    color: white;
    font-size: 12px;
    z-index: -1;
}

.search__logo {
    font-size: 24px;
    z-index: -1;
}

@media screen and (max-width: 830px) {

    .searchtop__right {
        flex: 2;
        gap: 5px;
    }

    .searchtop__centre {
        display: none;
    }

    .search__containeer {
        margin-left: 0px !important;
    }

    .language {
        display: none;
    }
}

@media screen and (max-width: 600px) {

    .search__containeer {
        margin-left: 0px !important;
    }

    .search__containeer {
        margin-left: 0;
    }

    .search__place {
        width: 50%;
    }

    .search__logo {
        margin-left: 34px;
    }
}

@media screen and (max-width: 380px) {
    .searchtop__centre {
        display: none;
    }

    .searchtop__centre {
        display: none;
    }

    .searchtop__wrapper {
        flex-direction: column;
        gap: 1rem;
    }

    .search__place {
        width: 100%;
    }

}
