.categorieslist__container {
    flex: 1;
    height: 70vh;
    margin: 3px;
    position: relative;
}

.categories__image img {
    height: 100%;
    width: 100%;
}

.categories__image {
    height: 100%;
    width: 100%;
    object-fit: cover;
}

.categories__info {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    display: flex;
    justify-content: center;
    width: 100%;
    align-items: center;
    flex-direction: column;
}

.categories__info-title {
    margin-bottom: 20px;
    color: white;
    text-align: center;
}

.categories__btn{
    border: none;
    padding: 10px;
    background: white;
    color: gray;
    cursor: pointer;
    font-weight: 600;
    align-items: center;
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 380px) {
    .categories__image img {
        height: 50vh;
    }
}