.login__container {
    height: 100vh;
    width: 100vw;
    background: linear-gradient( rgba(255, 255, 255, 0.5), rgba(255, 255, 255, 0.5) ),url(https://images.pexels.com/photos/6984661/pexels-photo-6984661.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940) center;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login__container-total {
    padding: 20px;
    width: 40%;
    background: white;
    padding: 20px;
}

.login__title {
    font-size: 24px;
    font-weight: 300;
}

form.login__from {
    display: flex;
    flex-wrap: wrap;
}

form.login__from>input {
    flex: 1;
    min-width: 40%;
    margin: 20px 10px;
    padding: 10px;
}

button {
    width: 66%;
    border: none;
    padding: 15px 20px;
    background-color: teal;
    color: white;
    cursor: pointer;
    border-radius: 10px;
    display: block;
    margin: 5px auto;
}

.login__container-total>a {
    margin: 5px auto;
    font-size: 12px;
    text-decoration: underline;
    cursor: pointer;
    display: block;
}

span.desc {
    color: gray;
    margin: 20px 0px !important;
}

span.desc>p {
    color: red;
    font-weight: bold;
    text-decoration: underline;
    padding: 5px;
}

@media screen and (max-width: 600px) {
    .login__container-total {
        padding: 20px;
        width: 80%;
        background: white;
        padding: 20px;
    }
}