.container__cart {
    padding: 20px;
}

h3.cart__title {
    font-weight: 300;
    text-align: center;
}

.cart__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.cart__texts span {
    margin: 0px 10px;
    cursor: pointer;
    text-decoration: underline;
}

.cart__bottom {
    display: grid;
    grid-template-columns: 80% 20%;
}

.cart__bottomm-info {
    display: flex;
    gap: 2rem;
}

.cart__product-detail {
    display: grid;
    grid-template-columns: 80% 20%;
    align-items: center;
}

.cart__summary {
    border: 0.5px solid lightgray;
    border-radius: 10px;
    padding: 20px;
    height: 60vh;
    flex: 1;
}

.summary__item {
    display: flex;
    justify-content: space-between;
    margin: 13px 0px;
}

button.summary__btn {
    padding: 10px;
    width: 60%;
    background-color: black;
    color: white;
    font-weight: 600;
    border: navajowhite;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.cart__product {
    display: flex;
    align-items: center;
}

.product__detail>span>p {
    font-weight: bold;
}

.price__icon-detail {
    display: flex;
    gap: 1rem;
    margin-top: 2rem;
}

.price__icon-detail {
    display: flex;
    gap: 1rem;
    margin: 1rem 0;
    color: black;
    align-items: center;
}


@media screen and (max-width: 1024px) {
    .cart__bottom {
        grid-template-columns: 1fr;
    }

    .cart__product-detail {
        grid-template-columns: 60% 35%;
        width: 100%;
    }

    .cart__product {
        flex-direction: column;
    }

    .cart__summary {
        height: 49vh;
        width: 75%;
        margin: 0 auto;
        margin-top: 2rem;
    }
}

@media screen and (max-width: 600px) {
    .cart__texts {
        display: none;
    }

    .cart__bottomm-info {
        flex-direction: column;
    }

    .cart__summary {
        height: 45vh;
    }
}



