.newsletter__container {
    display: flex;
    background-color: #fcf5f5;
    flex-direction: column;
    height: 60vh;
    justify-content: center;
    align-items: center;
}

.newsletter__title> h3 {
    font-size: 70px;
    font-weight: bold;
    margin-bottom: 20px;
}

.newsletter__desc> p {
    font-size: 20px;
    margin-bottom: 20px;
    font-weight: bold;
    color: gray;
}

.newsletter__input {
    width: 50%;
    background-color: white;
    height: 40px;
    justify-content: space-between;
    border: 1px solid lightgray;
    display: flex;
    text-align: center;
}

.newsletter__input>input {
    padding-left: 8px;
    flex: 8;
    border: none;
}

button.newsletter__btn {
    flex: 1;
    background-color: teal;
    color: white;
    border: none;
    height: 100%;
    border-radius:0%;
    margin: 0px;
}

@media screen and (max-width: 380px) {
    .newsletter__desc> p {
        text-align: center;
    }

    .newsletter__input {
        width: 80%;
    }

    .newsletter__title> h3 {
        font-size: 50px;
    }
}